       ;;  ;;  ;    ;  ;   ;  ;;;   ; ;    ;;
           ; ;;      ;;     ;;  ;    ;
                                     ;
                                    ;
                                   ;

*/

body {
  background-color: var(--half-episcopal-blue);
  margin: 0px;
  max-width: 100vw;

  > {

    * {
      box-sizing: border-box; /* so 100% widths work right */
      width: 100%;
    }

    header.site-header,
    main,
    footer {
      padding-right: var(--lhs-offset-padding);
      padding-left: var(--lhs-offset-padding);
    }

    header.site-header,
    footer {
        padding-top: 0.5rem;
    }
  }
}
