*/

/* Site header stuff */

header.site-header {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: 1rem;
    grid-template-areas: "logo brent-house"
                         "logo episcopal-at-uchicago"
                         /*"site-nav site-nav"*/;
    /* background-color: var(--half-episcopal-blue); */
    /* 0.4rem is usually 8 px, like default body margin */
    padding-bottom: 0.5rem;

    > {
        h1 {
            grid-area: brent-house;
            margin-top: 0px;
            margin-bottom: 0px;
            font-size: var(--h1-size);
            font-weight: bold;
        }

        h1 + p {
            grid-area: episcopal-at-uchicago;
            margin-top: 0px;
            margin-bottom: 0px;
            font-size: var(--larger-font-size);
        }

        .site-logo {
            grid-area: logo;
            align-self: center;

            img {
                max-width: 30vw; /* calc(var(--the-max-width) / 3); */
                max-height: 5rem;
            }
        }

    }
}

/*

             ;;
                    ;;
                    ;;
     ;;;;  ;;;;    ;;;;;     ;;            ;; ;;   ;;;;   ;     ;
   ;;  ;     ;;     ;;      ;  ;           ;;; ;       ;   ;   ;
    ;        ;;     ;;      ;  ;   ;;;;;   ;;  ;;      ;;  ;   ;
     ;;      ;;     ;;     ;;;;;;          ;;  ;;   ;;;;;   ; ;
       ;;    ;;     ;;      ;              ;;  ;;  ;   ;;   ; ;
   ;   ;     ;;      ;      ;  ;           ;;  ;; ;;   ;;   ; ;
    ;;;    ;;;;;;     ;;;    ;;            ;;  ;;   ;;; ;    ;


*/

body > nav.site-nav {

    background-color: var(--solid-episcopal-blue);

    > ul {
        list-style: none;
        padding: 0px;
        margin: 0px;
        max-width: 100%;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        align-items: stretch;
        justify-content: stretch;

        > li {
            /*flex: 1;*/
            display: block;
            box-sizing: border-box;
            margin: 0px;


            $v-padding: 0.5rem;
            $mid-padding: 1rem;

            & > {
                a:link, a:visited {
                    display: block;
                    box-sizing: border-box;
                    height: 100%;
                    padding-top: $v-padding;
                    padding-bottom: $v-padding;
                    color: white;
                    font-family: "Fira Sans", sans;
                    font-weight: normal;
                    text-decoration: none;
                }
                a:hover, a:active {
                    background-color: var(--maroon-solid);
                }
            }
            &:first-child {
                flex: 1 auto;
                text-align: left;
                border-left: none;
                > a:link {
                    padding-left: var(--lhs-offset-padding);
                    padding-right: $mid-padding;
                }
            }
            &:not(:first-child):not(:last-child) {
                flex: 2 auto;
                text-align: center;
                > a:link {
                    padding-left: $mid-padding;
                    padding-right: $mid-padding;
                }
            }
            &:last-child {
                flex: 1 auto;
                text-align: right;
                > a:link {
                    padding-left: $mid-padding;
                    padding-right: var(--lhs-offset-padding);
                }
            }

            &:not(:first-child) {
                border-left: calc(var(--body-part-border-width) / 2)
                                 solid
                                 black;
            }
        }
    }
}

/*

                     ;;


  ;;;; ;;  ;;;;    ;;;;    ;; ;;
  ;; ;; ;;     ;     ;;    ;;; ;
  ;; ;; ;;     ;;    ;;    ;;  ;;
  ;; ;; ;;  ;;;;;    ;;    ;;  ;;
  ;; ;; ;; ;   ;;    ;;    ;;  ;;
  ;; ;; ;;;;   ;;    ;;    ;;  ;;
  ;; ;; ;;  ;;; ;  ;;;;;;  ;;  ;;


*/

main {
    background-color: #ffffff;
    padding-top: 0.5rem;
    padding-bottom: 1rem; /* in addition to parskip from from final child */
    display: grid;
    grid-template-areas: ".   . header "
                         "lhs . content";
    grid-template-columns: minmax(var(--lhs-min-width),
                                  var(--lhs-max-width))
                                 minmax(var(--gutter-min-width),
                                        var(--gutter-max-width))
                                 minmax(var(--page-body-min-width),
                                        var(--page-body-max-width));
    grid-gap: 0px;

    > {
        * {
            box-sizing: border-box;
        }

        header {
            grid-area: header;
            max-width: var(--page-body-max-width);
            h2 {
              margin-top: 0;
            }
        }

        aside {
            grid-area: lhs;
            max-width: var(--lhs-max-width);
        }

        div.page-body {
            grid-area: content;
            max-width: var(--page-body-max-width);

            > :first-child {
                margin-top: 0;
                /*
                   If the first child has an upper margin,
                   it won't collapse with the margin of the `h2`.
               */
            }
        }
    }
}
