/*
  IDEA:
  use Photon SVG icon for nesting
*/
svg.photon-icon {
    width: 1em;
    height: 1em;
    margin: calc((1em * (var(--the-line-height) - 1)) / 2) 0;
    * {
        fill: currentColor;
    }
  }

/*
@counter-style empty-counter-style {
    system: cyclic;
    symbols: "";
}

@counter-style page-nav-depth-style {
    /* TODO: `speak-as` * /
    system: additive;
    range: 3 infinite;
    additive-symbols: 2 "▶", 1 " ";
    fallback: empty-counter-style;
    /*
       `suffix` is only used for `::marker`:
       https://www.w3.org/TR/css-counter-styles-3/#counter-style-suffix
       `::marker` isn’t a good choice b/c of a bug from 2000-04-22 (really):
       https://bugzilla.mozilla.org/show_bug.cgi?id=36854
       This is an exceptionally annoying workaround.
   * /
}
*/

main > aside > nav.page-nav {

    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    > div.sticky-shell {
        position: sticky;
        bottom: 0;
        min-height: min(100%,100vh);

        > ol {
            position: sticky;
            top: 0;
        }
    }


    counter-reset: page-nav-depth -1;

    ol {
        counter-increment: page-nav-depth 2;
        list-style-type: none;
        padding: 0px;
        margin: 0px;
    }

    ol::after {
        counter-increment: page-nav-depth -2;
        content: ""; /* needed or the effect is dropped */
    }

    li {
        margin: 0px;
        padding: 0px;
    }

    a:link, a:visited {
        display: flex;
        text-decoration: none;
        color: var(--solid-episcopal-blue);
        padding: calc(var(--tighter-parskip) / 2) var(--tighter-parskip);

        > {
            :first-child {
                flex: 0 max-content;
                &.nested-page-nav {
                    padding: 0 0.25em;
                }
            }
            :last-child {
                display: block;
                flex: 1 0px; /* consume whatever space is left */
                font-family: "Fira Sans", sans;
                font-weight: 300;
            }
        }
    }

    > div.sticky-shell > ol > li {
        &:not(:first-child) > a:link {
            padding-top: calc(var(--parskip) * 2);
        }
        &:last-child > a:link {
            /* match baseline of RHS */
            padding-bottom: var(--parskip);
        }
    }
/*
    a:link::before {
        display: block;
        flex: 0 max-content; /* fixed to needed size * /
        white-space: pre;
        content: counter(page-nav-depth, page-nav-depth-style);
    }
*/
    a:hover, a:active {
        color: white;
        background-color: var(--solid-episcopal-blue);
    }

}

