   ;;;;     ;;; ;  ;;  ;;   ;;  ;    ;;   ;; ;; ;; ;;;;;;    ;;;
   ;;
   ;;
   ;;


*/
@mixin pandemic-update-box-border($color: var(--maroon-solid)) {
    border: 0.1rem solid $color;
    border-radius: var(--pandemic-update-border-radius);
}

@mixin pandemic-update-box($max-width: var(--the-max-width)) {
    box-sizing: border-box;
    padding: var(--pandemic-update-gap);
    @include pandemic-update-box-border();
    margin: 0px
                var(--pandemic-update-gap)
                var(--parskip)
                var(--pandemic-update-gap);
    max-width: calc(#{$max-width} - (2 * var(--pandemic-update-gap)));
}

@mixin border-_-leftright-radius($topbottom, $radius) {
    @each $leftright in ("left" "right") {
        border-#{$topbottom}-#{$leftright}-radius: $radius;
    }
}

div.pandemic-update {
    @include pandemic-update-box($max-width: var(--the-max-width));
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: var(--pandemic-update-gap);
    grid-template-areas: "asclepius content";
    align-items: center;

    > {

        img {
            grid-area: asclepius;
            height: 5rem;
        }

        div {
            grid-area: content;

            > {
                p:first-child {
                    margin-top: 0px;
                    font-family: "Fira Sans", sans;
                    font-weight: 300;
                    color: var(--maroon-text);
                }

                :last-child {
                    margin-bottom: 0px;
                }
            }
        }
    }
}

/*


   ;;        ;;                       ;;;    ;;
   ;;                                ;
   ;;                    ;          ;;                   ;
   ;;;;    ;;;;      ;;;;;          ;;     ;;;;      ;;;;;
   ;;  ;     ;;     ;  ;           ;;;;;     ;;     ;  ;
   ;;  ;     ;;    ;;  ;;  ;;;;;    ;;       ;;    ;;  ;;
   ;;  ;;    ;;     ;  ;            ;;       ;;     ;  ;
   ;;  ;     ;;      ;;             ;;       ;;      ;;
   ;;  ;     ;;    ;;               ;;       ;;    ;;
   ; ;;    ;;;;;;   ;;;             ;;     ;;;;;;   ;;;
                       ;;                              ;;
                  ;;   ;                          ;;   ;
                    ;;;                             ;;;


*/

figure.big-fig {
    @include pandemic-update-box($max-width: 100%);
    clear: both;
    > {
        * {
            display: block;
            box-sizing: border-box;
        }
        figcaption, :not(figcaption) video {
            @include pandemic-update-box-border($color: black);
        }
        figcaption {
            width: 100%;
            margin: 0;
            background-color: var(--half-episcopal-blue);
            padding: var(--parskip);
            > span:only-child {
                display: block;
                box-sizing: border-box;
                max-width: calc(var(--the-max-width)
                                /*
                                  HACK: like the pandemic update box contents
                                  (looks better for short paragraphs)
                               */
                                - (4 * var(--pandemic-update-gap)));
                margin: auto;
            }
        }
        :not(figcaption) {
            max-width: 100%;
            margin-left: auto;
            margin-right: auto;
            video {
                object-position: center bottom;
                object-fit: contain;
            }
        }
        @each $pos, $outside, $inside in (("first" "top" "bottom")
                                          ("last" "bottom" "top")) {
            figcaption:#{$pos}-child, :not(figcaption):#{$pos}-child video {
                @include border-_-leftright-radius($inside, 0);
                border-#{$inside}: none;
            }
            :not(figcaption):#{$pos}-child {
                &, * {
                    @include border-_-leftright-radius(
                        $outside,
                        var(--pandemic-update-border-radius));
                }
            }
        }
    }
}

