  ;;;;;;    ;;            ; ;;      ;              ;;
                                    ;             ;
                                   ;
                                  ;


*/

.aspect-ratio-box {
    /*
      h/t
      https://stackoverflow.com/a/10441480/2523780
      https://medium.com/bleeding-edge/enforcing-an-aspect-ratio-on-an-html-element-in-react-and-css-27a13241c3d4
    */
    box-sizing: border-box;
    * { box-sizing: border-box; }
    width: 100%;
    height: 0;
    position: relative;
    > :only-child {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        > :only-child {
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    /* https://github.com/sass/sass/issues/255 */
    &.w16h9 {
        padding-bottom: 56.25%; /* calc(9 / 16) */
    }
    &.w1280h720 {
        @extend .w16h9;
        max-width: 1280px;
        max-height: 720px;
    }
}

/*


      ;;; ;;;;
     ;      ;;                      ;;
    ;;      ;;                      ;;
    ;;      ;;       ;;    ;;;;    ;;;;;     ;;    ;;  ;;
   ;;;;;    ;;      ;  ;       ;    ;;      ;  ;    ; ;;;
    ;;      ;;      ;  ;       ;;   ;;      ;  ;    ;;
    ;;      ;;     ;;  ;;   ;;;;;   ;;     ;;;;;;   ;;
    ;;      ;;      ;  ;   ;   ;;   ;;      ;       ;;
    ;;       ;      ;  ;  ;;   ;;    ;      ;  ;    ;;
    ;;       ;;;     ;;     ;;; ;     ;;;    ;;    ;;;;


*/
.floater {
    clear: both;
}

@mixin floaterAsFloat() {
    .floater {
        float: right;
        max-width: 45%;
        margin: 0px 0px 2rem 1rem;
        > img {
            width: calc(0.7 * var(--the-max-width));
            max-width: 100%;
        }
    }
}
@include floaterAsFloat();

@mixin floaterAsBlock($overhead) {
    @media (max-width: MediaRem((0.914 * $the-max-width) + $overhead)) {
        .floater {
            float: none;
            max-width: 100%;
            margin: 0px 0px var(--parskip) 0px;

            > img {
                width: 100%;
            }
        }
    }
}

/*
  Breakpoint states, from widest to narrowest:

    1. multiple columns, `.floater`s float with text wrapping
    2. multiple columns, `.floater`s fill whole body area
    3. single column, `.floater`s float with text wrapping`
    4. single column, `.floater`s fill whole body area

  It would be nice to express #2 with:
  ```
        @media (max-width: x) and (min-width: y) { ... }
  ```
  but Firefox didn't smoothly transition from state #2 to state #3
  if they are written that way.
  Instead, we repeat `.floater`'s #1 style under the rule for #3.
*/

$page-container-overhead: (2 * $page-container-horizontal-padding);
$multicol-min-overhead: $page-container-overhead
                            + $lhs-fixed-width
                            + $gutter-min-width;


@include floaterAsBlock($overhead: $multicol-min-overhead);


$multicol-min-width: $multicol-min-overhead + $page-body-min-width;

@media (max-width: MediaRem($multicol-min-width)) {
    @include floaterAsFloat();

    .page-container {
        grid-template-areas: "lhs" "header" "content";
        grid-template-columns: minmax(auto,calc(100vw - 2rem));
    }
}

@include floaterAsBlock($overhead: $page-container-overhead);

















